.scenario-container {
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 50px; /* Adjust this value to add space between scenarios */
  }
  
  
  .scenario-container + .scenario-container {
    margin-top: 20px; /* Add margin between each scenario container */
  }
  
  .choices button {
    margin-right: 10px;
    margin-bottom: 10px;
    padding: 10px;
    border: none;
    background-color: #007bff;
    color: #fff;
    cursor: pointer;
  }
  
  .feedback {
    margin-top: 20px;
  }
  
  .feedback p {
    font-size: medium;
    color: red;
    font-style: italic;
  }
  